
<script>
export default {
    name:'tab',
    // 由于tab是嵌套在tabs里面
    // 且tab有label这个属性，可以看成是向slot传递信息
    // 此时可以用props对属性进行监听
    props:{
        label:{
            type:[String],
            default:'tab'
        },
        index:{
            type:[String,Number],
            default:1
        }
    },
    mounted(){
        console.log(this.$parent.currentIndex);
        console.log(this);
        // this.$parent.pans.push(this.$slots.default)
        this.$parent.pans.push(this)
    },
    methods:{
        clickItemHandle(){
            // 要實現點擊高亮得改變parent.currentIndex
            // this.$parent.currentIndex=this.label
            // console.log('點擊');
            console.log(this.index);
            // 接下來就是把tab.index的值傳給tabs再由tabs傳給App
            this.$parent.getIndex(this.index)

        }
    },
    computed:{
        isActive(){
            if(this.$parent.currentIndex==this.index){
                return true
            }
            else{
                return false
            }

            
        }
    },
    render(){
        let className={
            tab:true,
            active:this.isActive
        }
        // computed定義的方法不需要加括號就能調用
        console.log(this.isActive);
        return(
            <li onclick={this.clickItemHandle} class={className}>{this.label}</li>
        )
    }
}
</script>

<style scoped>

    .tab {
    flex: 1;
    list-style: none;
    line-height: 40px;
    margin-right: 30px;
    position: relative;
    text-align: center;
}

.tab.active {
    border-bottom: 2px solid blue;
}
</style>