<template>
  <div>
    <h3>中國地圖</h3>

    <tabs :currentIndex="currentIndex" @onIndex="getIndexHandle">
      <tab index="1" label="中国地图">
        <div id="map" class="map"></div>
      </tab>
      <tab index="2" label="世界地圖">
        <div id="worldMap" class="map"></div>
      </tab>
    </tabs>
  </div>
</template>

<script>
import api from "../api";
export default {
  data() {
    return {
      currentIndex: 1,
    };
  },
  methods: {
    getIndexHandle(data) {
      this.currentIndex = data;
    },
  },
  mounted() {
    // 获取城市名称这个字段
    // console.log(res.data.retdata[0].xArea
    // console.log(res.data.retdata[0].curConfirm
    api
      .provinceNcov()
      .then((res) => {
        let allCitys = [];
        for (let i = 0; i < res.data.retdata.length; i++) {
          var temp = {
            name: res.data.retdata[i].xArea,
            value: res.data.retdata[i].curConfirm,
          };
          allCitys.push(temp);
        }
        // console.log(allCitys);
        this.$charts.chinaMap("map", allCitys);
        // this.$charts.worldMap("worldMap", []);
      })
      .catch((error) => {
        error;
      });
    // 测试地图
    api.getWorldNcov().then(res =>{
        console.log('数据',res);
        let allCountries = [];
        for(let i = 0;i<res.data.retdata.length;i++){
            var temp = {
                name:res.data.retdata[i].xArea,
                value:res.data.retdata[i].curConfirm
            }
            allCountries.push(temp)
        }
        // console.log(allCountries);
        this.$charts.worldMap("worldMap",allCountries)

    }
    ).catch(error=>{error})
  },
};
</script>

<style>
.map {
  width: 375px;
  height: 400px;
}
</style>