
<script>
export default {
  render(){
    console.log('received')
    return(
      // 上面JS定义变量
      // 下面写JSX的语句
      
      <div>{
        this.pans.map((ele,index)=>{
          // console.log('dd'+ele);
      
          // 用isActive判断 如果为true就显示，如果false就不显示
          return  <div style={{display:ele.isActive?'block':'none'}}>{ele.$slots.default}</div>
        })
      }</div>
    )
  },
  props:{
    pans:{
      type:[Array],
      default:()=>{
        return []
      }
    }
  }
}
</script>

