<template>
  <div class='case-num'>
    <div class="container">
        <div class="title">
            <!-- 截止时间 -->
            <!-- 一开始先写死 后期用Date()方法获取当前时间 -->
            <span>截止至: <b>{{formatDate(caseNum.timeStamp)}}</b></span>
        </div>
    </div>
    <div class="num">
        <ul class='count'>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:red'>{{numZero(caseNum.currentConfirmedIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.currentConfirmedCount}}</strong>
                    <span>現存確診 </span>
            </li>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:rgb(246,76,49)'>{{numZero(caseNum.confirmedIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.confirmedCount}}</strong>
                    <span>累計確診</span>
            </li>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:rgb(246,76,49)'>{{numZero(caseNum.suspectedIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.suspectedCount}}</strong>
                    <span>累計境外輸入</span>
            </li>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:rgb(246,76,49)'>{{numZero(caseNum.curedIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.curedCount}}</strong>
                    <span>累計治愈</span>
            </li>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:rgb(246,76,49)'>{{numZero(caseNum.deadIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.deadCount}}</strong>
                    <span>累計死亡</span>
            </li>
            <li class="create-item">
                <div class='create-count'>
                    <!-- 加粗 较昨日+数字?-->
                    <b>較昨日<em style='color:rgb(246,76,49)'>{{numZero(caseNum.seriousIncr)}}</em></b>
                </div>
                    <strong style="color:rgb(247,76,49">{{caseNum.seriousCount}}</strong>
                    <span>現存無症狀</span>
            </li>
        </ul>
    </div>
  </div>
</template>

<script>

export default {
    props:{
        caseNum:{
            type:[Object],
            default:{}
        },
        },
        methods:{
            numZero(num){
                return num>=0?'+'+num:num
            },
            formatDate(date){
                // 將時間戳傳進來
                var date=new Date(date);
                var YY=date.getFullYear()+'-';
                var MM=(date.getMonth()+1)<10?"0"+(date.getMonth()+1)+'-':date.getMonth()+1+"-";//0-11
                var DD=date.getDate()<10?'0'+date.getDate()+' ':date.getDate()+' ';
                console.log((date.getHours()<10)?'0'+date.getHours()+':':date.getHours()+':');
                var HH=(date.getHours()<10)?'0'+date.getHours()+':':date.getHours()+':';
                var mm=date.getMinutes()<10?'0'+date.getMinutes()+':':date.getMinutes()+':'
                var ss=date.getSeconds()<10?'0'+date.getSeconds():date.getSeconds();
                return YY+MM+DD+HH+mm+ss
            }
        }
    

}
</script>
