import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:'/city/:cityName',
    name:'City',
    component:()=>import('../views/City.vue'),
    props:true,
  }
]

const router = new VueRouter({
  // base: process.env.BASE_URL,  // 即vue.config.js的publicPath值
  // mode: 'history',
  routes,
})

export default router
