<template>
  <div>
    <!-- 对名字fieldValue->city -->
    <!-- 負責將選擇的值呈現在頁面上 -->
    <van-field
    
      v-model="city"
      is-link
      readonly
      :label="label"
      placeholder="请选择所在地区"
      @click="show = true"
    />
    <van-popup v-model="show" round position="bottom">
      <van-cascader
        v-model="cascaderValue"
        title="请选择所在地区"
        :options="options"
        @close="show = false"
        @finish="onFinish"
      />
    </van-popup>
  </div>
</template>

<script>
// 初始化设定
export default {
  data() {
  
    return {
      // 默认隐藏
      show: false,
      city: "",
      // options:this.options,
      // 选择的城市所在地区的名字
      cascaderValue: "",
    };
  },
  props: {
    label: {
      type: [String],
      default: "",
    },
    options: {
      type: [Array],
      default: ()=>{return [];},
    },
  },
  methods: {
    onFinish({ selectedOptions }) {
      this.show = false;
    //   將選擇呈現在頁面上 
      this.city = selectedOptions.map((option) => option.text).join("/");
      this.$emit('onValue',selectedOptions[1])
    },
    // showPopup() {
    //   this.show = true;
    // },
  },
  mounted(){
    console.log(this.options);
  }
};
</script>

