import axios from '../utils/request'
import base from './base'
const api={
    getNcov(params){
        console.log('f',{params});
        return axios.get(base.baseUrl+base.ncov,
            {params}
        )
    },
    // 得到全国各省市的疫情数据
    provinceNcov(params){
        return axios.get(base.baseUrl+base.all,{
            params
        })
    },
    // 得到世界各国的疫情数据
    getWorldNcov(){
        return axios.get(base.baseUrl+base.foreign)
    },
    getGuoneiNcov(params){
        return axios.get(base.baseUrl+base.guonei,{
            params
        })
    },
    getSwiperImg(){
        return axios.get(base.baseUrl+base.ncovimg)
    },
    getCitys(params){
        return axios.get(base.baseUrl+base.city,{
            params
        })
    }


}
export default api;