<template>
  <div class="spring">
    <h3 class="title">疫情期間防疫政策</h3>
    <!-- 放置联级菜单 -->
    <div>
        <Cascader :options='options' label='出發城市' @onValue='getFromCity' />
        <Cascader :options='options' label='到達城市' @onValue='getToCity'/>
        <van-button type='info' block>查看政策</van-button>
    </div>
  </div>
</template>

<script>
import Cascader from '../components/Cascader.vue'
import api from '../api'
export default {
    data(){
        return{
            options:[],
        }
    },
    components:{
        Cascader
    },
    methods:{
        getFromCity(fromCity){
            console.log(fromCity);
        },
        getToCity(toCity){
            console.log(toCity);
        }
    },
    mounted(){
        // 獲取城市數據
        api.getCitys({
            key:'171e165a7d991c5f6ecd5194c54778ef'
        }).then(res=>{
            // console.log('城市數據',res);
            var currentAll=[];
            for(let i=0;i<res.data.result.length;i++){
                var arr=[];
                for(let j=0;j<res.data.result[i].citys.length;j++){
                    var temp2={
                        text:res.data.result[i].citys[j].city,
                        value:res.data.result[i].citys[j].city_id
                    }
                // 省裡面的關係
                arr.push(temp2)
                }
            var temp1={
                text:res.data.result[i].province,
                value:res.data.result[i].province_id,
                children:arr
            }
            // 把得到的每個省和children城市的對象放到currentAll裡面[{province1,text,children:{text:?,city:?}},{province2,text,children:{text:?,city:?}},{province3,text,children:{text:?,city:?}}]
            currentAll.push(temp1);
            }
            this.options=currentAll
            // console.log('kkk',currentAll);
        }).catch(error=>{
            console.log(error);

        })
    }
};
</script>

<style>
.spring {
  width: 100%;
  background: #fff;
  padding: 10px;
  box-sizing: border-box;
}
.spring .title {
  font-size: 0.16rem;
  margin: 0 0 0.08rem 0.16rem;
}
</style>